<script setup>
import { useRouter } from 'vue-router'

const router = useRouter()

const logout = () => {
  // Clear all localStorage data
  localStorage.clear()
  router.push('/login')
}
</script>

<template>
  <button @click="logout" class="logout-btn">Logout</button>
</template>

<style scoped>
.logout-btn {
  padding: 8px 16px;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.logout-btn:hover {
  background-color: #d32f2f;
}
</style>